const addNew = (data) => {
  return axios({
    method: 'post',
    url: '/api/blog/new',
    data,
    headers: {
      'Content-Type': 'application/json',
    },
  })
    .then((res) => {
      if (res && res.data) {
        return res.data
      }
      throw new Error('Failed to get the data!')
    })
    .catch((err) => {
      // catch error first before send data to front end
      if (err) {
        console.error(err)
      }
    })
    .then((res) => {
      return Promise.resolve(res) // res could be undefined or data
    })
}

export { getList, getDetail, addNew }
